<template>
  <div class="login">
    <!-- 顶部 -->
    <header-panel></header-panel>
    <div class="content">
      <img :src="welcome" class="welcome">
      <el-card class="login-form-layout">
        <el-form autoComplete="on" :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left">
          <h2 class="login-title color-main">{{isLogin ? '请登录' : '注册账号'}}</h2>
          <el-form-item prop="username">
            <el-input name="username" type="text" v-model="loginForm.username" autoComplete="on" placeholder="请输入手机号/邮箱">
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input name="password" :type="pwdType" @keyup.enter.native="handleLogin" v-model="loginForm.password" autoComplete="on" placeholder="请输入密码">
              <span slot="suffix" @click="showPwd">
                <svg-icon icon-class="eye" class="color-main"></svg-icon>
              </span>
            </el-input>
          </el-form-item>
          <el-form-item prop="inviteCode" v-if="!isLogin">
            <el-input name="inviteCode" type="text" v-model="loginForm.inviteCode" autoComplete="on" placeholder="请输入邀请码">
            </el-input>
          </el-form-item>

          <el-form-item style="margin-bottom: 27px;text-align: center">
            <el-button class="button" style="width: 100%" type="primary" :loading="loading" @click.native.prevent="isLogin ? handleLogin() : handleRegister()">
              {{isLogin ? '登录' : '注册'}}
            </el-button>
          </el-form-item>

          <div class="bottom">
            <div class="tips" @click="test">没有账号？</div>
            <div class="register" @click="switchPage">{{isLogin ? '立即注册' : '立即登录'}}</div>
          </div>
        </el-form>
      </el-card>
    </div>
    <bottom-panel></bottom-panel>
  </div>
</template>

<script>
import HeaderPanel from '@/components/HeaderPanel'
import BottomPanel from '@/components/BottomPanel'
import {isvalidUsername} from '@/utils/validate';
import welcome from '@/assets/images/welcome.png';
import {setSupport,getSupport,setCookie,getCookie} from '@/utils/support';

export default {
  name: 'login',
  components: {
    HeaderPanel,
    BottomPanel,
  },
  data() {
    const validateUsername = (rule, value, callback) => {
      !isvalidUsername(value) ? callback(new Error('请输入正确的用户名')) : callback()
    };
    const validatePass = (rule, value, callback) => {
      value.length < 3 ? callback(new Error('密码不能小于3位')) :callback()
    };
    const validateInviteCode = (rule, value, callback) => {
      if (value.length < 4) {
        callback(new Error('邀请码不得少于4位'))
      } else if (value.length > 4) {
        callback(new Error('邀请码不得少于4位'))
      } else {
        callback()
      }
    };
    return {
      welcome,
      loginForm: {
        username: '',
        password: '',
        inviteCode: '',
      },
      loginRules: {
        username: [{required: true, trigger: 'blur', validator: validateUsername}],
        password: [{required: true, trigger: 'blur', validator: validatePass}],
        inviteCode: [{required: true, trigger: 'blur', validator: validateInviteCode}]
      },
      loading: false,
      pwdType: 'password',
      isLogin: true,
    }
  },
  created() {
    this.loginForm.username = getCookie("username");
    this.loginForm.password = getCookie("password");
    if(this.loginForm.username === undefined||this.loginForm.username==null||this.loginForm.username===''){
      this.loginForm.username = 'admin';
    }
    if(this.loginForm.password === undefined||this.loginForm.password==null){
      this.loginForm.password = 'macro123';
    }
  },
  methods: {
    showPwd() {
      this.pwdType = this.pwdType === 'password' ? '' : 'password'
    },
    switchPage () {
      this.loginForm = {
        username: '',
        password: '',
        inviteCode: '',
      }
      if (this.isLogin) {
        this.isLogin = false
      } else {
        this.isLogin = true
      }
    },
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true;
          this.$store.dispatch('Login', this.loginForm).then(() => {
            this.loading = false;
            setCookie("username", this.loginForm.username,15);
            setCookie("password", this.loginForm.password,15);
            this.$router.push({path: '/'})
            setTimeout(() => {
              location.reload()
            }, 1500);
          }).catch(() => {
            this.loading = false
          })
        } else {
          console.log('参数验证不合法！');
          return false
        }
      })
    },
    handleRegister () {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true;
          this.$store.dispatch('Register', this.loginForm).then(() => {
            this.loading = false;
            this.$message({ message: '注册成功，请登录', type: 'success', duration: 3000 });
            this.isLogin = true
          }).catch(() => {
            this.loading = false
          })
        } else {
          console.log('参数验证不合法！');
          return false
        }
      })
    },
    test () {
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.login {
  background: #eeeeee;
  height: 100%;
  .content {
    display: flex;
    align-items: center;
    margin: 0 auto;
    width: 1160px;
    padding-top: 60px;
    .welcome {
      width: 582px;
      height: 394px;
    }
    .login-form-layout {
      width: 400px;
      height: 480px;
      margin: 140px auto;
      line-height: 20px;
      border-radius: 8px;
      background-color: rgba(255, 255, 255, 100);
      text-align: center;
      box-shadow: 0px 2px 6px 0px rgba(145, 145, 145, 17);
      .login-title {
        text-align: center;
        margin: 27px 0 44px 0;
      }
      .bottom {
        display: flex;
        flex-direction: row;
        margin: 0 auto;
        width: 150px;
        .tips {
          color: rgba(102, 102, 102, 100);
          font-size: 14px;
        }
        .register {
          color: rgba(3, 31, 187, 100);
          font-size: 14px;
          cursor: pointer;
        }
      }
      .button {
        width: 100%;
        background: #00224a;
      }
    }
  }
}
</style>



